.elements {
  display: grid;
  grid-template-columns: repeat(auto-fit,282px);
  justify-content: center;
  gap: 20px 17px;
  padding: 50px 0 66px 0;
  margin: 0 auto;
  max-width: 880px;
}

@media screen and (max-width: 915px) {
  .elements {
    width: 88%;
  }
}

@media screen and (max-width: 620px) {
  .elements {
    padding: 36px 0 48px 0;
  }
}